@font-face {
  font-family: "Gotham Pro";
  src: url("data:font/woff2;charset=utf-8;base64,") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("data:font/woff2;charset=utf-8;base64,") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  font-family: "Gotham Pro", sans-serif;
  font-size: 16px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
}
@media screen and (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
}
@media screen and (max-width: 992px) {
  .container {
    max-width: 720px;
  }
}
@media screen and (max-width: 768px) {
  .container {
    max-width: none;
    padding: 0 15px;
  }
}

.logo {
  display: flex;
}
.logo__wrapper {
  display: flex;
  align-items: center;
}
.logo__link {
  text-decoration: none;
}
.logo__image {
  margin-right: 8px;
}
.logo__text {
  color: #171717;
  font-weight: bold;
}

.menu__list {
  display: flex;
}

.social__list {
  display: flex;
  align-items: center;
}
.social__item {
  margin-right: 20px;
}
.social__item:last-child {
  margin-right: 0;
}

.header {
  background-color: #FFFFFF;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}